@import url(https://fonts.googleapis.com/css?family=Bitter:700|Noto+Sans+TC&display=swap);
body{
	font-size: 18px;
	background: #fff;  /* fallback for old browsers */
}
a { 
	color: #000; 
	color: initial; 
	text-decoration: none;
} 
a:hover{
	text-decoration: none;
	color: #000;
	color: initial;
}
td{
	background-color: white;
}
.main-container{
	margin: 0px 8% 0px 8%;
	text-align: center;
}

.first-container{
	background-image: url(/iot-lab/static/media/home2.212e8f96.png);
	padding: 2% 7% 2% 7%;
	/*
	background: #1c92d2;  /* fallback for old browsers */
	/*background: -webkit-linear-gradient(to right, #f2fcfe, #1c92d2);  /* Chrome 10-25, Safari 5.1-6 */
	/*background: linear-gradient(to right, #f2fcfe, #1c92d2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	color: #444;
	background-size:100% 100%;
	font-family: 'bitter';
}
.first-container img{
	float: left;
	width: 40px;
}
.first-container h5{
	/*line-height: inherit;*/
	margin-left: 50px;
	font-family: arial;
}
.breadcrumb{
	font-size: 0.8rem;
}

.navbar{
	background-color: #1273DE;
	padding-left: 7%;
}

.navbar-brand{
	display: none;
}
.navbar-items{
	float: right;
	color: #fff;
}
.nav-item{
	font-family: 'Microsoft YaHei', sans-serif;
	font-size: 1.3rem;
	-webkit-transition: .3s;
	transition: .3s;
	margin-left: 3px;
	margin-right: 3px;
	border: 1.5px solid transparent;
}
.nav-item:hover{
	-webkit-transition: .3s;
	transition: .3s;
	border: 1.5px solid #FFF;
}

.index-container{
	min-height: 70vh;
	background-color: #f3f3f3;
	padding: 3% 5% 5% 5%;
}

.backgroundcircle{
	background-color: #1273DE;
	border-radius: 100px;
	width: 200px;
	height: 200px;
	display: flex;
	align-items: center;
	text-align: center;
	justify-content: center;
}
.backgroundcircle img{
	width: 150px;
}
.backgroundcircle:hover{
	-webkit-transform: scale(1.03);
	        transform: scale(1.03);
}

.words p{
	margin: 0;
	padding: 10px 0px 0px 0px;
	font-weight: 700;
}

.footer-container{
	padding: 2%;
	align-items: center;
	background-color: #555;
	text-align: center;
}
.footer{
	color: #f3f3f3;
}

.advisor-container{
	display: flex;
	justify-content: center;
	color: #555;
	margin: 100px 0px 100px 0px
}

.prof-name p{
	font-family: 'Open Sans', sans-serif;
	font-size: 2rem;
	font-weight: 600;
}
.prof-name ul{
	font-size: 1.2rem;
	list-style-type: none;
}
.bio-container{
	display: flex;
	justify-content: center;
	padding-left: 30px;
	padding-right: 30px;
	text-align: justify;
}

.buttons{
	display: flex;
	justify-content: space-around;
}

.tst{
	display: flex;
	justify-content: center;
}

.yahei{
	font-family: 'Microsoft YaHei';
}

.newstitle{
	-webkit-transition: .3s;
	transition: .3s;
	color: #0E90D2;
}
.newstitle:hover{
	text-decoration: underline;
}
.pubulication-table thead{
	background-color: azure;
}
.pubulication-table td{
	font-size: 0.9rem;
}

.leftpart{
	display: block;
	max-width: 25%;
	float: left;
	height: 100% ;
	padding-top: 10px;
	vertical-align:middle;
	box-sizing: border-box;
}
.leftpart img{
	width: 100%;
}
.rightpart{
	display: block;
	padding-left: 20px;
	width: 75%;
	float: right;
}

.my_link:hover{
	text-decoration: underline;
	font-weight: bold;
}
.dropdown:hover>.dropdown-menu{
		display: block;
}
.dropdown:active>.dropdown-menu{
	display: block;
}

@media screen and (max-width: 480px) {
  .first-container{
		display: none;
	}
	.main-container{
		margin:0px;
	}
	.navbar-brand{
		display: inline;
		display: initial;
	}
}

